






import { Vue, Component, Prop, Emit } from 'vue-property-decorator';

@Component({
  name: 'YButton',
})
export default class extends Vue {
  @Prop({
    type: String,
    validator: (size: string) => {
      return ['default', 'small', 'large'].includes(size);
    },
    default: 'default',
  })
  private size!: 'default' | 'small' | 'large';

  @Prop({
    type: String,
    validator: (size: string) => {
      return ['default', 'primary'].includes(size);
    },
    default: 'default',
  })
  private type!: 'default' | 'primary';

  @Emit('click')
  private onEmitClick(e: MouseEvent): MouseEvent {
    return e;
  }
}
